.advantages__list {
  max-width: 1222px;
  list-style: none;
  padding: 120px 48px 60px 48px;
  margin: 0 auto;
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
  box-sizing: content-box;

  @include vp-1023 {
    padding: 79px 48px 40px 48px;
    column-gap: 25px;
    row-gap: 28px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    padding: 50px 25px 30px 24px;
    max-width: 595px;
    gap: 30px;
  }
}

.advantages__item:hover {
  background-color: $color-deep-sky-blue;

  & h2,
  p {
    color: $color-alice-blue;
  }

  & svg {
    color: $color-alice-blue;
  }
}

.advantages__item {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 595px;
  min-height: 200px;
  padding: 40px 40px 35px 44px;
  border: 1px solid #ebebeb;

  @include vp-1023 {
    padding: 40px 43px 27px 46px;
    min-height: 360px;
  }

  @include vp-767 {
    min-width: 210px;
    padding: 29px 30px 35px 30px;
    min-height: 340px;
  }

  & svg {
    margin-bottom: 20px;
    color: $color-deep-sky-blue;

    @include vp-1023 {
      margin-bottom: 16px;
    }

    @include vp-767 {
      width: 30px;
      height: 30px;
      margin-bottom: 16px;
    }
  }

  & h2 {
    margin: 0 0 13px 0;
    font-family: $ff-roboto-condensed;
    font-weight: 400;
    font-size: 24px;
    line-height: 34px;
    color: $color-deep-sky-blue;

    @include vp-1023 {
      margin-bottom: 8px;
    }

    @include vp-767 {
      font-size: 20px;
      line-height: 28px;
      margin-bottom: 9px;
    }
  }

  & p {
    margin: 0;
    min-height: 120px;
    max-width: 505px;
    font-family: $ff-PT-Mono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-black-pearl;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 19px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    width: 5px;
    height: 100%;
    background: $color-deep-sky-blue;
    top: 0;
    left: 0;
  }
}


.advantages__item:nth-child(3n) {
  @include vp-1023 {
    padding-top: 42px;

    & h2 {
      margin-bottom: 6px;
    }

    & svg {
      margin-bottom: 14px;
    }
  }

  @include vp-767 {
    padding-top: 29px;
    min-height: 325px;

    & h2 {
      margin-bottom: 13px;
    }

    & svg {
      margin-bottom: 19px;
    }
  }
}


.advantages__item:nth-child(4n) {
  @include vp-1023 {
    padding: 42px 43px 35px 47px;

    & svg {
      margin-bottom: 11px;
    }

    & h2 {
      margin-bottom: 3px;
    }
  }

  @include vp-767 {
    padding: 29px 43px 35px 30px;
    min-height: 325px;

    & svg {
      margin-bottom: 19px;
    }

    & h2 {
      margin-bottom: 13px;
    }
  }
}
